.App {
  font-family: sans-serif;
  text-align: center;
}

.header {
  color: white;
  font-family: "Raleway";
  background-color: black;
  margin: 0 !important;
}

h3 {
  margin-left: 20px;
  font-family: "Raleway";
}

img {
  height: 250px;
  width: 200px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

.lightPage {
  background-color: #feb377;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  color: white;
  text-align: center;
  font-family: "Raleway";
}

.darkPage {
  background-color: #00a8cc;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  color: white;
  text-align: center;
  font-family: "Raleway";
}

body {
  margin: 0;
}

html {
  height: 500px;
  margin: 0;
}

li {
  padding: 10px;
}

h2 {
  margin: 0 !important;
  padding: 15px;
}

h1 {
  margin-top: 20px;
}
